div::-webkit-scrollbar {
    width: 8px;
}
  
  /* Track */
div::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
}
   
  /* Handle */
div::-webkit-scrollbar-thumb {
    background: #f57b25; 
    border-radius: 10px;
}
  
  /* Handle on hover */
div::-webkit-scrollbar-thumb:hover {
    background: #dd610a; 
}

